
.player-input .word.valid {
  text-shadow: 0 0 5px rgb(3, 211, 38), 0 0 5px rgb(3, 211, 38);
}

.player-input .word.mistake {
  text-shadow: 0 0 5px rgb(211, 3, 72), 0 0 6px rgb(211, 3, 72), 0 0 7px rgb(211, 3, 72), 0 0 7px rgb(211, 3, 72);
  color: rgb(243, 176, 187)
}

.player-input .word.banned {
  text-shadow: 0 0 6px rgb(211, 72, 3), 0 0 7px rgb(211, 72, 3), 0 0 8px rgb(211, 72, 3);
  color: rgb(241, 186, 177);
}

.player-input .word.dupe {
  text-shadow: 0 0 6px rgb(187, 0, 0), 0 0 7px rgb(187, 0, 0), 0 0 8px rgb(187, 0, 0);
  color: rgb(250, 145, 145);
}

.player-input .space {
  position: relative;
}

.player-input.checked .space:after {
  position: absolute;
  content: '';
  top: 0.15em;
  left: -0.2em;
  right: -0.2em;
  bottom: -0.3em;
  background: red;
  opacity: 0.6;
  border-radius: 0.1rem;
  z-index: -1;
  transform: skew(-15deg);
}

.player-input {
  font-size: 2.5rem;
}