
.results-display {
  display: grid;
  grid-template-columns: 1fr 3.5rem 3.5rem;
}

.results-display--total {
  font-weight: bold;
  font-size: 1.2em;
}

.results-display__title {
  text-align: right;
  padding-bottom: 0.25rem;
  padding-right: 1rem;
}

.results-display__result {
  text-align: center;
}

.results-display__points {
  text-align: center;
}