
.round-countdown {
  pointer-events: none;
}


.round-countdown__number {
  position: fixed;
  top: 23%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.round-countdown__number span {
  font-weight: bold;
  font-size: 3rem;
  color: #fff;
  text-shadow:
  -0.15em -0.15em 0 #101010,
  0.15em -0.15em 0 #101010,
  -0.15em 0.15em 0 #101010,
  0.15em 0.15em 0 #101010,
  0 -0.15em 0 #101010,
  0 0.15em 0 #101010,
  -0.15em 0 0 #101010,
  0.15em 0 0 #101010;
}
